/**-----------------------------------------------------------------------------------------------
* + manage page
 * -----------------------------------------------------------------------------------------------
 */
@keyframes input-roll {
  0% {
    transform: rotateX(0deg) translateZ(-10px); }
  100% {
    transform: rotateX(360deg) translateZ(-10px); } }

@keyframes radar-roll {
  0% {
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    transform: translate(-50%, -50%) rotate(360deg); } }

