@import './var';
@import './dev';

/**-----------------------------------------------------------------------------------------------
* + manage page
 * -----------------------------------------------------------------------------------------------
 */
%manage-page-top-bottom {
      width: $manage-page-content-width;
      height: $manage-page-top-bottom-height;
}

%input-control-box {
      width: 20px;
      height: $input-control-box-height;
      position: relative;

      @include bkcolor(#eeeeee);
}

%square-box {
      width: $suqare-box-width;
      height: $suqare-box-width;
      position: relative;
}

%radar {
      width: $suqare-box-width - 10px;
      height: 2px;

      @include bkcolor(#340166);
      @extend %ct;

      animation-name: radar-roll;
      animation-duration: 2s;
      animation-iteration-count: infinite;
}

%clipboard-data-circle {
      @include eq-wh(36px);
      @include circle;
}

%clipboard-box {
      width: 100px;
      height: 80px;
}

@keyframes input-roll {
      0% {
            transform: rotateX(0deg) translateZ(-10px);
      }

      100% {
            transform: rotateX(360deg) translateZ(-10px);
      }
}

@keyframes radar-roll {
      0% {
            transform: translate(-50%, -50%) rotate(0deg);
      }

      100% {
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

%flush-board {
      position: absolute;
      top: 0;
      left: 0;
      transition: all 300ms ease;
      width: 0;
      height: 100%;
      opacity: 1;
      @include bkcolor($flush-to-ui-color);
      z-index: -999;

      &.show {
            width: 100%;
      }
}

/**-----------------------------------------------------------------------------------------------
* + data control
 * -----------------------------------------------------------------------------------------------
 */
%data-flow-appearance {
      @include eq-wh($data-flow-size);
      @include circle;
      @include bkcolor(#ffffff);
}

/**-----------------------------------------------------------------------------------------------
 * + font family
  * -----------------------------------------------------------------------------------------------
  */
%font {
      font-family: "Adobe Garamond Pro";
}