/**-----------------------------------------------------------------------------------------------
 * + define borders
 * -----------------------------------------------------------------------------------------------
 */
%showOutline {
      outline: 1px solid #ffdd33 !important;

      & *:hover {
            outline: 1px solid gold !important;
      }
}

%border {
      border: 1px solid gold;
      box-sizing: border-box;
}

%border-1 {
      border: 1px solid #eeeeee;
      box-sizing: border-box;
}

%border-2 {
      border: 1px solid #dddddd;
      box-sizing: border-box;
}

%border-3 {
      border: 1px solid #cccccc;
      box-sizing: border-box;
}

%border-4 {
      border: 1px solid #bbbbbb;
      box-sizing: border-box;
}

%border-5 {
      border: 1px solid #aaaaaa;
      box-sizing: border-box;
}

%border-6 {
      border: 1px solid #777777;
      box-sizing: border-box;
}

/**-----------------------------------------------------------------------------------------------
 * + define layout
 * -----------------------------------------------------------------------------------------------
 */
%ct {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
}

%v-ct {
      position: absolute;
      top: 50%;
      transform: translate(0px, -50%);
}

%h-ct {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0px);
}

%in-layout {
      display: inline-block;
      vertical-align: middle;
      box-sizing: border-box;
}

/**-----------------------------------------------------------------------------------------------
 * + define mixin
 * -----------------------------------------------------------------------------------------------
 */
// @-> spirit image
@mixin spirit-pos($x, $y) {
      background-position: $x $y;
}

@mixin spirit($imgURL, $x, $y) {
      background-image: url($imgURL);
      @include spirit-pos($x, $y);
}

// @-> div arrow
@mixin arrow($direction, $color, $size) {
      width: 0px;
      height: 0px;
      border: $size solid #ffffff;

      @if $direction=="t" {
            border-color: $color transparent transparent transparent;
      }

      @if $direction=="r" {
            border-color: transparent $color transparent transparent;
      }

      @if $direction=="b" {
            border-color: transparent transparent $color transparent;
      }

      @if $direction=="l" {
            border-color: transparent transparent transparent $color;
      }
}

// @-> background-color
@mixin bkcolor($color) {
      background-color: $color;
}

// @-> trasition quick set
@mixin ani-tra {
      transition: all $ani-transition-time ease;
}



// @-> clear both
@mixin clearfix {
      content: '';
      display: block;
      clear: both;
}

// @-> equal width & height
@mixin eq-wh($size) {
      width: $size;
      height: $size;
}

// @ -> circle
@mixin circle {
      border-radius: 50%;
}

// @ -> ellipsis
@mixin ellipsis {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}